import React from 'react'

const SubscribeAM = () => {
	function blockDefaultTransition(selector, event) {
		event.preventDefault();

		selector.classList.add('active')
	}

	return (
		<section id="subscribe">
	      <div className="container">
	        <h3>Բաժանորդագրվեք մեր նորություններին և առաջարկներին</h3>
	        <form>
	          <div className="flex-row">
	            <input placeholder="Խնդրում ենք մուտքագրել ձեր էլ. փոստի հասցեն" type="text" />
	            <button 
	            className="flex-row__btn btn" 
	            type="submit"
	            onClick={event => blockDefaultTransition(document.querySelector('.subscribe-message'), event)}
	            >Ուղարկել
	            </button>
	          </div>
	          <div className="subscribe-message">
	            <i></i>
	            <p className="mt-20">Շնորհակալություն բաժանորդագրության համար !</p>
	          </div>
	        </form>
	      </div>
	    </section>
	)
}

export default SubscribeAM