import React, { Component } from 'react';
import { Link, animateScroll as scroll } from "react-scroll";

export default class Scroll extends Component {
	  scrollToTop = () => {
    	scroll.scrollToTop();
  	};

  	render() {
  		return (
  			<Link
            activeClass="active"
            to={this.props.scrollTo}
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
        />
  		);
  	}
}
