import React from 'react'
import '../../../styles/subscribe.css'

const SubscribeRU = () => {
	function blockDefaultTransition(selector, event) {
		event.preventDefault();

		selector.classList.add('active')
	}

	return (
		<section id="subscribe">
	      <div className="container">
	        <h3>Подписывайтесь на наши новости и предложения</h3>
	        <form>
	          <div className="flex-row">
	            <input placeholder="Просим ввести ваш адрес электронной почты" type="text" />
	            <button
	            className="flex-row__btn btn"
	            type="submit"
	            onClick={event => blockDefaultTransition(document.querySelector('.subscribe-message'), event)}
	            >Отправить
	            </button>
	          </div>
	          <div className="subscribe-message">
	            <i></i>
	            <p className="mt-20">Спасибо за подписку!</p>
	          </div>
	        </form>
	      </div>
	    </section>
	)
}

export default SubscribeRU