import React from 'react'
import '../../../styles/subscribe.css'

const SubscribeEN = () => {
	function blockDefaultTransition(selector, event) {
		event.preventDefault();

		selector.classList.add('active')
	}

	return (
		<section id="subscribe">
	      <div className="container">
	        <h3>Subscribe to our news and offers</h3>
	        <form>
	          <div className="flex-row">
	            <input placeholder="Please enter your email address" type="text" />
	            <button
	            className="flex-row__btn btn"
	            type="submit"
	            onClick={event => blockDefaultTransition(document.querySelector('.subscribe-message'), event)}
	            >Send
	            </button>
	          </div>
	          <div className="subscribe-message">
	            <i></i>
	            <p className="mt-20">Thanks for subscribing!</p>
	          </div>
	        </form>
	      </div>
	    </section>
	)
}

export default SubscribeEN